import { Book } from "@/types";
import TagsContainer from "./tagsContainer";

const BookEntry : React.FC<Book>= ({ Title, Author, ISBN }) => {
    return(
        <div className="flex">
            <div className="w-1/2 p-2 h-fit bg-slate-200 rounded-md border-solid border border-slate-500">
                <div className="flex space-x-4">
                    <div className="text-base font-bold capitalize">{ Title }</div>
                    <div className="text-base">{ Author }</div>
                </div>
                <div className="text-sm text-right"><b>ISBN: </b>{ ISBN }</div>
                <div className="flex space-y-3">
                    <div className="text-sm">description</div>
                    <TagsContainer />
                </div>
            </div>

        </div>
    );
};

export default BookEntry;